import axios from 'src/utils/axios'

export const getList = ({filters, sort, page, limit}) => new Promise((resolve, reject) => {  
  axios.get('/api/borrowing-history/fetch', { params: {  sort, page, limit, filters} 
  })
  .then((response) => {
    
    if(!response){
      reject({
        resultFlag: false,
        value: null,
        errorCode: 'Something is wrong.',
        errorMessage: `response is ${response}`,
        activityId: null
      })
      return
    }
    resolve({
      resultFlag: true,
      value: response.data
    })
  })
  .catch((error) => {
    reject({
      resultFlag: false,
      value: null,
      errorCode: 'Something is wrong.',
      errorMessage: error.message,
      activityId: null
    })
  })
})

